body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: #003c6c;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: top;
  color: white;
  padding: 10px;
}

.gradient {
background-image: -webkit-linear-gradient(right, #003c6c , #fdc700);
background-image: linear-gradient(to left, #003c6c , #fdc700);
background-color: #fdc700; /* For browsers that do not support gradients */
height: 10px;
margin-top: 7px;
}

.title{
  font-family: "EB Garamond";
  font-size:calc(30px + 2vmin);
  color: #fdc700;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.name{
    display: inline-block;
    text-align: center;
    font-size: 3vmin;
    width: 33%;
    color: white;
}

.capacity{
    display: inline-block;  
    text-align: center;
    font-size: 3vmin;
    width: 33%;
    color: white;
}

.rating{
    display: inline-block;
    text-align: center;
    font-size: 3vmin;
    width: 33%;
    color: white;
}

.bigButtons {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.boxList {
    padding: 0;
    margin: 0;
}

.boxItem {
    width: 100%;
    -ms-box-sizing: border-box;
    box-sizing: border-box;

    /*background-image: linear-gradient(to left, rgb(0,60,108) , rgb(253,199,0));
    background-color: #fdc700; /* For browsers that do not support gradients */

    background-color: rgb(0,60,108);
    
    border: 1.5px rgba(253, 199, 0, 0);
    border-radius: 20px;

    margin-top: 15px;
    cursor: pointer; 

    transition-timing-function: ease-out;
    transition-duration: 0.2s;
    /* Safari Support*/
    -webkit-transition-timing-function: ease-out;
    -webkit-transition-duration: 0.2s;
}

.boxItemHelper {
    /*Use for adding gradient transitions. Set as just background transition for now*/
    background-color: rgba(0,60,108, 0);
    border: 1.5px solid #fdc700;
    border-radius: 20px;

    padding-top: 6vh;
    padding-bottom: 6vh;

    width: 100%;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    }

.boxItem:hover {
    background-color: rgb(253,199,0,0.8);
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24),0 17px 50px 0 rgba(0,0,0,0.19);
    transition-timing-function: ease-out;
    transition-duration: 0.3s;
    /* Safari Support*/
    -webkit-transition-timing-function: ease-out;
    -webkit-transition-duration: 0.3s;
}

.quickNavList {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.quickNav {
    float: left;
    border-right:1px solid rgb(187, 187, 187);
}

.quickNav:last-child {
    border-right: none;
}

.listButton {
    background-color: #003c6c;
    color: white;
    cursor: pointer;    
    padding: 10px;
    width: 100%;
    border: none;
    text-align: center;
    font-size: 15px;
    text-decoration: none;
}

.listButton:hover {
    background-color: #00266C;
}

.active {
    background-color: #fdc700;
    color: black;
}

.NavLink {
    color:white;
}
.listFood {
    background-color: #fdc700;
    color: black;
    cursor: pointer;    
    padding: 10px;
    width: 100%;
    border: none;
    text-align: center;
    font-size: 15px;
